.container {
    max-width: 1080px;
    margin: 0 auto;
}

.sidebar {
    border-right: 1px solid #f0f2f1;
    width: 250px;
    float: left;
    padding: 35px;
}

.sidebar > ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.sidebar > ul > li {
    font-size: 14px;
    font-weight: 400;
    padding: 0 0 10px;
    margin: 1em 0 0;
}

.sidebar a {
    line-height: 1.5;
}

.sidebar > ul > li > ul {
    border-top: 1px dashed rgba(0,0,0,.1);
    display: block;
    list-style: none;
    margin: .5em 0 0;
    padding: .5em 0 0;
    font-size: 14px;
}

.docs article {
    padding: 25px 125px 50px 50px;
    margin-left: 300px;
}

.docs article ul {
    font-size: 14px;
}

.docs article ul li {
    margin: 8px;
    line-height: 1.65;
}