h1 {
    font-size: 48px;
    font-weight: 200;
}

p {
    line-height: 1.5;
    margin: 10px 0 20px;
}

p code {
    background: #f0f2f1;
    padding: 1px 5px;
}

code,
kbd,
pre,
samp {
    font-family: source-code-pro, monospace;
    font-size: 14px;
}

blockquote {
    background: #f2f2f2;
    margin: 10px 0 20px;
    padding: 10px 20px;
    border-radius: 5px;
}

a, p code {
    color: #2e827e;
}

.btn.focus, .btn:focus, .btn:hover, a {
    text-decoration: none;
}

.docs article p {
    font-size: 14.5px;
    line-height: 1.7;
}

.docs article h3:first-of-type {
    margin-top: 15px;
}

.docs article h3 {
    font-size: 30px;
    font-weight: 400;
    margin-top: 55px;
    position: relative;
}

.docs article h1+ul {
    margin: 0;
    padding: 0 0 50px;
    list-style: none;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.5;
    border-bottom: 1px solid #f0f2f1;
    -webkit-font-smoothing: antialiased;
}

pre code {
    padding: 15px !important;
    font-size: 15px;
    border-radius: 5px;
}

.docs article a {
    text-decoration: underline;
}